export const getRangeTrackBackground = ({ values, colors, min, max }) => {
    const direction = {
        Right: 'to right',
        Left: 'to left',
        Down: 'to bottom',
        Up: 'to top'
    };
    const progress = values.map(value => ((value - min) / (max - min)) * 100);
    const middle = progress.reduce(
        (acc, point, index) =>
            `${acc}, ${colors[index]} ${point}%, ${colors[index + 1]} ${point}%`,
        ''
    );
    return `linear-gradient(${direction.Right}, ${colors[0]} 0%${middle}, ${
        colors[colors.length - 1]
        } 100%)`;
};