import React from 'react'
import { Range } from 'react-range';

import { getRangeTrackBackground } from '../../utils'
import styles from './styles'

const RangeSelector = ({ small, medium, label, valuePrefix, value, onChange, min, max, step, containerClass, containerStyle }) => {

    const renderTrack = ({ props, children }) => {
        const lineBackground = getRangeTrackBackground({ min, max, values: value, colors: styles.trackLineColors });
        return (
            <div
                onMouseDown={props.onMouseDown}
                onTouchStart={props.onTouchStart}
                style={{ ...props.style, ...styles.trackContainer }}
            >
                <div ref={props.ref} style={{ ...styles.trackLine, background: lineBackground }}>
                    {children}
                </div>
            </div>
        )
    };

    const renderThumb = ({ index, props, isDragged }) => {
        const valueLabel = `${valuePrefix || ''}${value[index].toFixed()}`;
        const thumbValueLabelColor = isDragged ? styles.thumbValueLabelColor.dragged : styles.thumbValueLabelColor.stationary;
        let width = small ? '42px' : (medium ? '68px' : '88px');

        return (
            <div {...props} style={{ ...props.style, ...styles.thumbContainer, width }}>
                <span style={{ ...styles.thumbValueLabel, color: thumbValueLabelColor }}>{valueLabel}</span>
                <span style={styles.thumbLabel}>{label}</span>
            </div>
        )
    };

    return (
        <div className={containerClass} style={{ ...styles.container, ...containerStyle }}>
            <Range
                values={value}
                step={step}
                min={min}
                max={max}
                onChange={(values) => onChange(values)}
                renderTrack={renderTrack}
                renderThumb={renderThumb}
            />
        </div>
    )
}

export default RangeSelector

