import React, { useEffect, useState } from 'react';
import M from 'materialize-css';

import RangeSelector from '../../components/RangeSelector'
import { AMOUNT_RANGE, AMOUNT_RANGE_STEPS, CABIN_RANGE, CABIN_RANGE_STEPS, CONFERENCE_RANGE, CONFERENCE_RANGE_STEPS, WORKSTATION_RANGE, WORKSTATION_RANGE_STEPS, SUPER_AREA_RANGE, SUPER_AREA_RANGE_STEPS, COVERED_AREA_RANGE, COVERED_AREA_RANGE_STEPS } from '../../constants';

const Search = ({ isVisible }) => {
    let buildingSelectorRef;
    let facingSelectorRef;
    let floorSelectorRef;

    const [amountRange, setAmountRange] = useState(AMOUNT_RANGE);
    const [cabinRange, setCabinRange] = useState(CABIN_RANGE);
    const [conferenceRange, setConferenceRange] = useState(CONFERENCE_RANGE);
    const [workstationRange, setWorkstationRange] = useState(WORKSTATION_RANGE);
    const [superAreaRange, setSuperAreaRange] = useState(SUPER_AREA_RANGE);
    const [coveredAreaRange, setCoveredAreaRange] = useState(COVERED_AREA_RANGE);

    useEffect(() => {
        M.FormSelect.init(buildingSelectorRef);
        M.FormSelect.init(facingSelectorRef);
        M.FormSelect.init(floorSelectorRef);
    }, [buildingSelectorRef, facingSelectorRef, floorSelectorRef, isVisible]);

    const renderBuildingSelector = () => (
        <div class="col s12 m6">
            <select ref={select => { buildingSelectorRef = select; }}>
                <option value="0">Select building name</option>
                <option value="1">Aditya Heights</option>
            </select>
        </div>
    );

    const renderFacingSelector = () => (
        <div class="col s12 m6">
            <select ref={select => { facingSelectorRef = select; }} >
                <option value="0">Select facing</option>
                <option value="1">North-East</option>
            </select>
        </div>

    );

    const renderFloorSelector = () => {
        return (
            <div class="col s12 m6">
                <select ref={select => { floorSelectorRef = select; }} >
                    <option value="0">Select floor</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                </select>
            </div>
        )
    }

    const renderAmountRangeSelector = () => {
        return (
            <RangeSelector
                label="Amount"
                valuePrefix="₹"
                value={amountRange}
                onChange={setAmountRange}
                min={AMOUNT_RANGE[0]}
                max={AMOUNT_RANGE[1]}
                step={AMOUNT_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderCabinRangeSelector = () => {
        return (
            <RangeSelector
                small
                label="Cabin"
                value={cabinRange}
                onChange={setCabinRange}
                min={CABIN_RANGE[0]}
                max={CABIN_RANGE[1]}
                step={CABIN_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderConferenceRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Conference"
                value={conferenceRange}
                onChange={setConferenceRange}
                min={CONFERENCE_RANGE[0]}
                max={CONFERENCE_RANGE[1]}
                step={CONFERENCE_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderWorkstationRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Work Station"
                value={workstationRange}
                onChange={setWorkstationRange}
                min={WORKSTATION_RANGE[0]}
                max={WORKSTATION_RANGE[1]}
                step={WORKSTATION_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderSuperAreaRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Super Area"
                value={superAreaRange}
                onChange={setSuperAreaRange}
                min={SUPER_AREA_RANGE[0]}
                max={SUPER_AREA_RANGE[1]}
                step={SUPER_AREA_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderCoveredAreaRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Covered"
                value={coveredAreaRange}
                onChange={setCoveredAreaRange}
                min={COVERED_AREA_RANGE[0]}
                max={COVERED_AREA_RANGE[1]}
                step={COVERED_AREA_RANGE_STEPS}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderAmenities = () => (
        <div className="row">
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>A/C</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Pantry</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Washroom</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Freehold</span>
                </label>
            </div>
        </div>
    );

    if (isVisible) {
        return null;
    }

    return (
        <div class="col s12">
            <div class="card">
                <div class="card-content">
                    <div className="row">
                        {renderBuildingSelector()}
                        {renderFacingSelector()}
                        {renderFloorSelector()}
                        {renderCabinRangeSelector()}
                        {renderConferenceRangeSelector()}
                        {renderWorkstationRangeSelector()}
                        {renderSuperAreaRangeSelector()}
                        {renderCoveredAreaRangeSelector()}
                        {renderAmountRangeSelector()}

                    </div>
                    {renderAmenities()}
                </div>
                <div class="card-action">
                    {/* <button className="btn waves-effect black">Clear Filter</button>
                    <button className="btn waves-effect black">Apply Filter</button>
                     */}
                    <a className="waves-effect btn white black-text">Clear</a>
                    <a className="waves-effect btn black right">Apply</a>
                </div>
            </div>
        </div>
    )
}

export default Search;