import React from 'react'

const PropertyItem = ({ data }) => {
  const { buildingName, floor, facing, address, amount, cabin, conference, workStation, isAC, isPantry, isWashroom, isFreeHold } = data;

  const getFloorSuffix = (i) => {
    let j = i % 10,
      k = i % 100;
    if (j === 1 && k !== 11) {
      return i + "st floor";
    }
    if (j === 2 && k !== 12) {
      return i + "nd floor";
    }
    if (j === 3 && k !== 13) {
      return i + "rd floor";
    }
    return i + "th floor";
  }

  const renderDetail = (value, name) => {
    return (
      <div className="col s4 m2 center">
        <h6 className="black-text">{value}</h6>
        <div className="grey-text">{name}</div>
      </div>
    );
  };

  const renderAmenities = (value, label) => {
    let iconClass = "red-text";
    let iconName = "close";
    let labelClass = "grey-text";

    if (value) {
      iconClass = "green-text";
      iconName = "check"
      labelClass = "grey-text";
    }

    return (
      <div className="col s3 center">
        <span className={iconClass}><i class="material-icons">{iconName}</i></span>
        <div className={labelClass}>{label}</div>
      </div>
    )
  };

  return (
    <div class="card">
      <div class="card-content">
        <span class="card-title activator grey-text text-darken-4">{buildingName}<i class="material-icons right">more_vert</i></span>
        <h6 class="grey-text">{`${getFloorSuffix(floor)} / ${facing}`}<span className="right green-text">{`₹${amount}`}</span></h6>
        <div className="row">
          {renderDetail(cabin, "Cabin")}
          {renderDetail(conference, "Conference")}
          {renderDetail(workStation, "Work station")}
          {renderDetail(`₹20/sq.ft.`, "Maintenance")}
          {renderDetail("8,000 sq.ft.", "Super Area")}
          {renderDetail("70*30", "Covered")}
        </div>
        <div className="row">
          {renderAmenities(isAC, "A/C")}
          {renderAmenities(isPantry, "Pantry")}
          {renderAmenities(isWashroom, "Washroom")}
          {renderAmenities(isFreeHold, "Freehold")}
        </div>
      </div>
      <div class="card-reveal">
        <span class="card-title grey-text text-darken-4">TITLE<i class="material-icons right">close</i></span>
        <p>Test</p>
      </div>
    </div>
  )
}

export default PropertyItem;